.breadcrumbs {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: var(--fontSizeBase500);
  line-height: var(--lineHeightBase500);
  font-weight: var(--fontWeightRegular); }
  .breadcrumbs__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: var(--spacingVerticalXS) var(--spacingHorizontalXS); }
    .breadcrumbs__item--clickable {
      cursor: pointer; }
      .breadcrumbs__item--clickable:hover {
        background-color: var(--colorNeutralBackground3Hover); }
    .breadcrumbs__item--active {
      font-weight: var(--fontWeightSemibold); }
  .breadcrumbs__chevron {
    margin-top: var(--spacingVerticalXS); }
    .breadcrumbs__chevron:last-child {
      display: none; }
