.file-picker-table .cell--bold button {
  font-weight: var(--fontWeightSemibold); }

.file-picker-table .cell--center {
  text-align: center; }
  .file-picker-table .cell--center button {
    justify-content: center; }

.file-picker-table .max-two-lines {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical; }

.file-picker-table .table-row-selected {
  background-color: var(--colorNeutralBackground1Selected); }
  .file-picker-table .table-row-selected .table-file-name {
    font-weight: var(--fontWeightSemibold); }

.tags-container {
  display: flex;
  flex-wrap: wrap;
  padding: var(--spacingVerticalXXS); }
  .tags-container .tag {
    padding: var(--spacingVerticalXXS);
    margin-right: var(--spacingVerticalXS);
    margin-bottom: var(--spacingVerticalXS);
    background-color: #e6e6e6; }

.file-picker-table__wrapper {
  border-radius: var(--borderRadiusLarge);
  box-shadow: 0px 0px 4px var(--colorNeutralShadowAmbient);
  border: 1px solid var(--colorNeutralBackground5); }
